import React, {MutableRefObject, useEffect, useMemo, useRef, useState} from 'react';
import {throttle} from "lodash";
import Typewriter from "typewriter-effect";

import AppBackground from './components/AppBackground';
import Header from "./components/Header";
import SlotMachine from "./components/area/front/SlotMachine";
import Decorations from "./components/area/main/Decorations";
import WhatWeDo from "./components/area/main/sections/WhatWeDo";
import Teammates from "./components/area/main/sections/Teammates";
import TypewriterWithPositionChecker from "./components/TypewriterWithPositionChecker";

import {
    NewLineIfNotDesktop,
    NewLineIfMobile, ContactEmail,
} from "./global/constants";

import {
    AppRoot, LightFont
} from "./styled_containers/AppContainers";

import {
    FrontArea, FrontContent, FrontTitle, AboutUs
} from "./styled_containers/areas/front/FrontContainer";

import {
    MainArea, MainContent
} from "./styled_containers/areas/main/MainContainer";

import {
    ContactArea, ContactContent, ContactMessage, ContactButton
} from "./styled_containers/areas/contact/ContactContainer";

import {
    FooterArea, FooterContent, FooterDetail, FooterItems, FooterLogo,
    CompanyName, CompanyInfo, CompanyZipCode, Copyright
} from "./styled_containers/areas/footer/FooterContainer";

import logo from "./assets/logo.png";

export type ScrollInto = {
    out: MutableRefObject<HTMLDivElement[]>
    index: number
}

const App: React.FC = () => {

    const [scrollValue, setScrollValue] = useState(0);

    const scrollIntoTargets = useRef<HTMLDivElement[]>([]);

    const throttledScroll = useMemo(
        () =>
            throttle(() => {
                setScrollValue(window.scrollY);
            }, 100),
        []);

    useEffect(() => {
        window.addEventListener("scroll", throttledScroll);
        return () => window.removeEventListener("scroll", throttledScroll);
    }, [throttledScroll]);

    return (
        <AppRoot>
            <AppBackground />
            <Header scrollIntoTargets={scrollIntoTargets.current} fixed={scrollValue > 10}/>
            <FrontArea>
                <FrontContent>
                    <SlotMachine size={5}/>
                    <FrontTitle>
                         
                        <span className={"CenteredTypewriter"}>
                            <Typewriter
                                onInit={(typewriter) => {
                                    const messages = [' ', ' ', ' ', ' '];

                                    const typewriterFunc = (index: number) => {
                                        setTimeout(() => typewriter.typeString(messages[index]).start(), 0);
                                        setTimeout(() => typewriter.deleteAll().start(), 3000);

                                        setTimeout(() => typewriterFunc(index + 1 >= messages.length ? 0 : index + 1), 5000);
                                    };

                                    typewriterFunc(0);
                                }}
                            />
                        </span>
                        
                    </FrontTitle>
                    {/* <FrontLogo src={logo} alt=""/> */}
                    <AboutUs onClick={() => scrollIntoTargets.current[0].scrollIntoView({behavior: "smooth"})}>  </AboutUs>
                    {/*<ReadMoreIcon src={viewMoreIcon} alt={""}/>*/}
                </FrontContent>
            </FrontArea>
            <MainArea>
                <MainContent>
                    {/* <WhoWeAre scrollInto={{out: scrollIntoTargets, index: 0}}/> */}
                    <WhatWeDo scrollInto={{out: scrollIntoTargets, index: 0}} currentScroll={scrollValue}/>
                    {/*<WeAreUnstablers currentScroll={scrollValue}/>*/}
                    <Teammates scrollInto={{out: scrollIntoTargets, index: 1}}/>
                    {/*<Partners/>*/}
                </MainContent>
                <Decorations/>
            </MainArea>
            <ContactArea>
                <ContactContent>
                    <ContactMessage>
                        <LightFont>&nbsp;</LightFont>
                        <NewLineIfNotDesktop/>
                        <TypewriterWithPositionChecker
                            currentScroll={scrollValue}
                            onInit={(typewriter) => {
                                typewriter.typeString('')
                                    .start();
                            }}
                        />
                        <br/>
                         !
                    </ContactMessage>
                    <a href={`mailto:${ContactEmail}`}><ContactButton> </ContactButton></a>
                </ContactContent>
            </ContactArea>
            <FooterArea>
                <FooterContent>
                    <FooterLogo src={logo} alt={""}/>
                    <FooterDetail>
                        <div>
                            <CompanyName> </CompanyName>
                            <FooterItems>
                                <div>
                                    <div onClick={() => scrollIntoTargets.current[0].scrollIntoView({behavior: "smooth"})}> </div>
                                </div>
                                <div>
                                    <div onClick={() => scrollIntoTargets.current[1].scrollIntoView({behavior: "smooth"})}> </div>
                                </div>
                            </FooterItems>
                        </div>
                        <div>
                            <CompanyInfo>
                                  : 473-81-02068 |  <br/>
                                <CompanyZipCode>15495</CompanyZipCode>    1 362 <NewLineIfMobile/> 5
                            </CompanyInfo>
                            <Copyright>
                                2021. team unstablers Inc. all rights reserved.
                            </Copyright>
                        </div>
                    </FooterDetail>
                </FooterContent>
            </FooterArea>
        </AppRoot>
    );
}

export default App;
