import React, {useCallback, useEffect, useMemo, useRef, useState} from "react";

import previewImage1 from "../../../../assets/sections/preview_image_1.png";
import previewImage2 from "../../../../assets/sections/preview_image_2.png";
import previewImage3_1 from "../../../../assets/sections/preview_image_3_1.png";
import previewImage3_2 from "../../../../assets/sections/preview_image_3_2.png";

import pagerLeftArrow from "../../../../assets/pager_arrow_left.png";
import pagerRightArrow from "../../../../assets/pager_arrow_right.png";

import {
    SectionRoot, SectionHeader, Subtitle, Title, YellowTypeWriter,
    ItemRoot1,
    ItemTexts, ItemTitle1, ItemTitleB1, ItemTitle2, ItemTitleB2, ItemTitle3,
    ItemContent1, ItemContent2, ItemContent3, ItemImageArea1, ItemImageArea2, ItemImageArea3,
    ItemImage1, ItemImage2, ItemImage31, ItemImage32,

    PagerControllerRoot, PagerControllerArrow, PagerIndicatorRoot, PagerIndicatorDot
} from "../../../../styled_containers/areas/main/sections/what_we_do/WhatWeDoContainers";

import {
    NewLineIfDesktop,
    NewLineIfMobile,
    NewLineIfNotMobile,
    VisibleIfNotMobile
} from "../../../../global/constants";
import TypewriterWithPositionChecker from "../../../TypewriterWithPositionChecker";
import PagerMemory, {generatePagerController, PagerController} from "../../../pager_memory/PagerMemory";
import {ScrollInto} from "../../../../App";

type Props = {
    currentScroll: number
    scrollInto: ScrollInto
}

const WhatWeDo: React.FC<Props> = (props) => {

    const rootElement = useRef<HTMLDivElement>(null);

    useEffect(() => {
        if (rootElement.current == null) return
        props.scrollInto.out.current[props.scrollInto.index] = rootElement.current;
    }, [props.scrollInto.index, props.scrollInto.out]);

    const pagerController1 = useMemo(() => generatePagerController(), []);

    const pager1Listener = useCallback((position: number) => {
        setPager1Selected(position);
    }, []);

    const [pager1Selected, setPager1Selected] = useState(0);

    return (
        <SectionRoot ref={rootElement}>
            <SectionHeader>
                <Subtitle>  </Subtitle>
                <Title>
                     &nbsp;
                    <YellowTypeWriter>
                        <TypewriterWithPositionChecker
                            currentScroll={props.currentScroll}
                            onInit={(typewriter) => {
                                typewriter.typeString('')
                                    .start();
                            }}
                        />
                    </YellowTypeWriter>
                    <NewLineIfDesktop/>
                    
                </Title>
            </SectionHeader>
            <ItemRoot1 className={`c${pager1Selected + 1}`}>
                <PagerMemory orientation={"horizontal"} dimension={{width: "100%", height: "100%"}}
                             listeners={{select: [pager1Listener]}}
                             controller={pagerController1}>
                    <div>
                        <ItemTexts>
                            <ItemTitle1><ItemTitleB1> </ItemTitleB1> <br/>  </ItemTitle1>
                            <ItemContent1>
                                React Native  iOS / Android  <br/>
                                <VisibleIfNotMobile>&nbsp;</VisibleIfNotMobile>  React Native   <br/>
                                Kotlin  Android   <br/>
                                Swift  iOS   
                            </ItemContent1>
                        </ItemTexts>
                        <ItemImageArea1>
                            { /*
                                <Description1>
                                    <DescriptionIcon1 src={ descriptionIcon1 }/>
                                      &nbsp;<NewLineIfNotMobile/>
                                     <NewLineIfMobile/><NewLineIfNotMobile/>
                                      &nbsp;<NewLineIfNotMobile/>
                                       
                                </Description1>
                            */ }
                            <ItemImage1 src={previewImage1} alt={""}/>
                        </ItemImageArea1>
                    </div>
                    <div>
                        <ItemTexts>
                            <ItemTitle2><ItemTitleB2> </ItemTitleB2>  <br/> </ItemTitle2>
                            <ItemContent2>
                                TypeScript  React.js    <br/>
                                Python 3  Django  RESTful API  <br/>
                                WebRTC  P2P   <br/>
                                PC        <br/>
                            </ItemContent2>
                        </ItemTexts>
                        <ItemImageArea2>
                            { /*
                                <Description2>
                                    <DescriptionIcon2 src={ descriptionIcon2 }/>
                                    PC  &nbsp;<NewLineIfDesktop/>
                                       <NewLineIfNotDesktop/> &nbsp;<NewLineIfDesktop/>
                                        
                                </Description2>
                            */ }
                            <ItemImage2 src={previewImage2} alt={""}/>
                        </ItemImageArea2>
                    </div>
                    <div>
                        <ItemTexts>
                            <ItemTitle3><ItemTitleB2>
                                </ItemTitleB2> <NewLineIfMobile/> <NewLineIfNotMobile/>Linux<NewLineIfMobile/> 
                                </ItemTitle3>
                            <ItemContent3>
                                      <NewLineIfMobile/>  <br/>
                                    <br/>
                                     <br/>
                            </ItemContent3>
                        </ItemTexts>
                        <ItemImageArea3>
                            <ItemImage31 src={previewImage3_1} alt={""}/>
                            <ItemImage32 src={previewImage3_2} alt={""}/>
                        </ItemImageArea3>
                    </div>
                </PagerMemory>
                <PagerControllerView controller={pagerController1} selected={pager1Selected} pageLength={3}/>
            </ItemRoot1>
        </SectionRoot>
    )
}

type PagerControllerProps = {
    controller: PagerController
    selected: number
    pageLength: number
}

const PagerControllerView: React.FC<PagerControllerProps> = (props) => {
    return (
        <>
            <PagerControllerRoot>
                <PagerControllerArrow enabled={props.selected !== 0} alt={""} src={pagerLeftArrow}
                                      onClick={() => props.controller.previous.call(null, true)}/>
                <PagerControllerArrow enabled={props.selected !== props.pageLength - 1} alt={""} src={pagerRightArrow}
                                      onClick={() => props.controller.next.call(null, true)}/>
            </PagerControllerRoot>
            <PagerIndicatorRoot>
                {(new Array(props.pageLength)).fill(0).map((_, index) => <PagerIndicatorDot key={`PagerIndicatorDot_${index}`}
                                                                                      selected={index === props.selected}/>)}
            </PagerIndicatorRoot>
        </>
    )
}

export default WhatWeDo;
